<template>
  <div id="inventory">
    <div
      v-for="(item, index) in items"
      :key="index"
      @click="selectMouse($event)"
      v-on:touchstart.passive="selectTouch($event)"
    >
      <div
        :style="{'border-color': index === selected ? 'white' : 'black', 'background-image': textures[item.type]}"
        class="item"
        :data-id="index.toString()"
      >
      <div>
        <p id="amount">{{item.amount}}</p>
        <p id="type">{{item.type}}</p>
      </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import {ref} from "vue"
class Item {
  type: string;
  amount: number;
  constructor(type = " ", amount = 0) {
    this.type = type;
    this.amount = amount;
  }
}

const numberOfItems = 10;
const items = Array.from(
  { length: numberOfItems },
  item => new Item("grass", 11)
);
const textures = {};

export default {
  setup(): { [key: string]: any } {
    return {
      items,
      selected: ref(0),
      textures
    };
  },
  methods: {
    selectMouse(event: MouseEvent) {
        this.selected = parseInt((event.target as any).dataset.id) || this.selected;
    },
    selectTouch(event: TouchEvent) {
        this.selected = parseInt((event.target as any).dataset.id) || this.selected;
    }
  }
};
</script>

<style lang="sass" scoped>
#inventory
    display: grid
    width: 80vw
    grid-auto-rows: auto
    grid-auto-flow: column
    grid-gap: 1.5vw

.item
    border: min(1vw, 2px) solid black
    border-radius: 0.5vw
    height: 0
    box-sizing: border-box
    padding-top: 100%
    background-color: rgba(200, 200, 200, 0.4)
    backdrop-filter: blur(3px)

.item div
    height: 100%
    width: 100%
    position: absolute
    pointer-events: none
    transform: translateY(0, -100%)
    display: flex
    justify-content: center
    align-items: center
    flex-direction: column

</style>
